<template>
    <div>
          <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-table :data="rgihtList" border  stripe style="width: 100%">
                <el-table-column type="index"></el-table-column>
                <el-table-column label="" prop="authName"></el-table-column>
                <el-table-column label="" prop="path"></el-table-column>
               
            </el-table>
        </el-card>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    created() {
        this.token = window.sessionStorage.getItem('token');
        this.getrightList();       
    },
   data() {
       return {
           addRoleDialogVisible: false,
           rgihtList: []
       }
   },
   methods: {
    getrightList() {
        let param = new URLSearchParams();
             param.append('token', this.token);
             axios({
                 method: 'post',
                 url: '/lightspace/rightList',
                 data: param
             }).then(this.handleGetRightSucc.bind(this))
               .catch(this.handleGetRightErr.bind(this))
        },
    handleGetRightSucc(res) {
      if(res.data.status === 10204) {
          this.$message.error(res.data.msg);
          this.$router.push('/lightspace/login');
      } else if(res.data.status == 200) {
          this.rgihtList = res.data.data;
      }
    },
    handleGetRightErr(error) {
        console.log(error)
    }, 
   }
  
}
</script>
<style lang="less" scoped>

</style>


